/* Searchwidth */
.width{
    width: 40rem;
}

.search-btn{
    background-color: white;
    padding-left:10px ;
    padding-right:10px ;
    border-radius: 10px;
    color: #2b3035;
    font-weight: 700;
    border-style: none;
    transition: 0.3s;
    border-style: solid;
    border-color: black;
    width: 150px;
}
.search-btn:hover{
    background-color: black;
    color: white;
}

.search-input{
    border-radius: 0px;
    border-style:solid;
    border-color: rgba(0, 0, 0, 0.4);
    border-radius: 6px;

}

